import { useEffect, useState } from "react";
import "./styles.css";
import axios from "axios";

export default function App() {
  const [data, setData] = useState([]);
  const [listItems, setListItems] = useState([]);

  function ajax(url) {
    return new Promise(function (resolve, reject) {
      axios
        .get(url)
        .then(function (response) {
          resolve(response);
        })
        .catch(function (err) {
          reject(err);
        });
    });
  }

  useEffect(() => {
    fetch("https://anapioficeandfire.com/api/books")
      .then((res) => res.json())
      .then((data) => setData(data));
  }, []);

  function displayPov(val) {
    Promise.all(val.map((url) => ajax(url)))
      .then((results) => {
        setListItems(results.map((item) => item.data));
      })
      .catch((err) => {});
  }

  return (
    <div style={{ display: "flex", justifyContent: "center", gap: "60px" }}>
      <div className="App">
        {data.map((item, id) => (
          <li key={id} onClick={() => displayPov(item.povCharacters)}>
            {item.name}
          </li>
        ))}
      </div>
      <div>
        <h4>Characters</h4>
        {listItems?.map((items2, id) => (
          <li key={id}>{items2.name}</li>
        ))}
      </div>
    </div>
  );
}
